import React, { useState } from "react";
import "./DropdownMenu.css"; // Το CSS αρχείο μας
import { icons } from "../lists";
import accounts from "../accounts";
export default function DropdownMenu({ setSelectedPlatform, platforms }) {
  const [isOpen, setIsOpen] = useState(false);

  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };

  let handleSelect = (platform) => {
    setSelectedPlatform(platform);
    setIsOpen(false);
  };

  let list = [accounts[0].code, accounts[1].code, accounts[2].code];
  if (platforms === "tasks") list = ["Finance", "Marketing","HR","Operations","IT","Customer Service", "Sales", "Logistics"];

  return (
    <div className="dropdown fade-in-2">
      <button className="dropdown-btn fade-in-1" onClick={toggleDropdown}>
        {platforms === "tasks" ? <h4>Categories</h4> : <h4>Platform</h4>}
        
        <img src={icons.icon_arrow_down} className="dropdown-icon" alt="" />
      </button>

      {isOpen && (
        <ul className="dropdown-menu">
          <li className="dropdown-item" onClick={() => handleSelect("All")}>
            All
          </li>
          {list.map((item) => (
            <li className="dropdown-item" onClick={() => handleSelect(item)}>
              {item}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
