import React from "react";
import { Link, useParams } from "react-router-dom";
import bookings from "../bookings";
import { icons } from "../lists";
import { setDate } from "../functions";
import accounts from "../accounts";
import TableContLine from "../components/TableContLine";
import Timeline from "./Timeline";
import Header from "../Header";
import "./BookingDetailsPage.css";
import InfoLine from "./InfoLine";
import TableHeader from "../components/TableHeader";

function BookingDetailsPage() {
  const { id } = useParams();
  const booking = bookings.find((b) => b.platform_reserv_code === id);

  if (!booking) {
    return <h2>Booking not found!</h2>;
  }

  let img = accounts[0].icon;
  for (let i = 0; i < accounts.length; i++) {
    if (booking.platform === accounts[i].code) {
      img = accounts[i].icon;
    }
  }

  const userOtherBookings = bookings.filter(
    (b) =>
      b.email === booking.email &&
      b.platform_reserv_code !== booking.platform_reserv_code
  );

  let classPayment = "payment-box-uncompleted";
  let infoPayment = "Not Completed";
  let imgPayment = icons.icon_x;
  if (booking.payment_status === "paid") {
    classPayment = "payment-box-completed";
    infoPayment = "Completed";
    imgPayment = icons.icon_checked;
  }

  return (
    <div className="page-div">
      <Header
        title="Booking Details"
        descr="Easily manage all bookings and guests in one place. One platform, everything seamlessly."
      />
      <div className="top-brd"></div>
      <div className="columns-50">
        <div className="col-50">
          <div className="component-title fade-in-1">Details</div>
          <div className="main-info-line">
            <div className="main-info-div">
              <div>
                <h2>{booking.name}</h2>
                <h4>{booking.platform_reserv_code}</h4>

                <p>{setDate(booking.check_in)}</p>
              </div>
            </div>
            <img src={img} alt="platform_logo" />
          </div>
        </div>
        <div className="col-50">
          <div className="component-title fade-in-1">Check-in Details</div>
          <Timeline booking={booking} />
        </div>
      </div>

      <div className="component-title fade-in-1">Contact Information</div>
      <div className="details-person-box fade-in-1">
        <InfoLine title="Name" info={booking.name} />
        <hr className="details-hr" />
        <InfoLine title="Email" info={booking.email} />
        <hr className="details-hr" />
        <InfoLine title="Tel. Number" info={booking.phone} />
        <hr className="details-hr" />
        <InfoLine title="Booking Date" info={setDate(booking.booking_date)} />
      </div>
      <div className="component-title fade-in-1">Payment Status</div>
      <div className={classPayment}>
        <div className="info-line">
          <div className="info-line">
            <img src={imgPayment} alt="user" className="payment-img" />
            <h3>{infoPayment}</h3>
          </div>
          <div>
            <h3>{booking.price_total} €</h3>
          </div>
        </div>
      </div>

      <div className="component-title fade-in-1">Other Bookings</div>
      {userOtherBookings.length > 0 ? <TableHeader /> : <div></div>}
      {userOtherBookings.length > 0 ? (
        userOtherBookings.map((booking, index) => (
          <Link to={`/booking/${booking.platform_reserv_code}`}>
            <TableContLine
              key={booking.platform_reserv_code || index}
              booking={booking}
              index={index}
            />
          </Link>
        ))
      ) : (
        <h4 className="no-bookings-message">No other bookings found!</h4>
      )}
    </div>
  );
}

export default BookingDetailsPage;
