import React from "react";
import Header from "../Header";
import accounts from "../accounts";
import "./Home.css";
import InfoBox from "./components/BoxInfo";
import bookings from "../bookings";
import MonthlyBookingTrendsChart from "../AnalyticsPage/MonthlyBookingTrendsChart";
import "../AnalyticsPage/Analytics.css";
import TableContLine from "../components/TableContLine";
import TableHeader from "../components/TableHeader";
import { Link } from "react-router-dom";

function HomePage() {
  const isMobile = window.innerWidth < 600;
  return (
    <div className="page-div">
      <Header
        title="Dashboard"
        descr="Easily manage all bookings and guests in one place. One platform, everything seamlessly."
      />
      <div className="top-brd"></div>
      <div className="component-title fade-in-1">Accounts</div>
      {isMobile ? (
        <>
          <div className="boxes-div">
            {accounts.slice(0, 2).map((account, index) => (
              <InfoBox
                key={account.code}
                {...account}
                index={index}
              />
            ))}
          </div>
          <div className="boxes-div">
            <InfoBox
              key={accounts[2].code}
              {...accounts[2]}
              index={2}
            />
          </div>
        </>
      ) : (
        <div>
          <div className="boxes-div">
            {accounts.map((account, index) => (
              <InfoBox
                key={index}
                {...account}
                index={index}
              />
            ))}
          </div>
        </div>
      )}

      <div className="component-title fade-in-2">Latest Bookings</div>

      <TableHeader />
      {bookings.slice(0, 3).map((booking, index) => (
        <Link to={`/booking/${booking.platform_reserv_code}`} key={booking.platform_reserv_code || index}>
          <TableContLine
            booking={booking}
            index={index}
          />
        </Link>
      ))}

      <div className="component-title fade-in-box">Monthly Booking Trends</div>
      <div className="chart-div">
        <MonthlyBookingTrendsChart />
      </div>
    </div>
  );
}

export default HomePage;
