import bookings from "../bookings";

function TotalBookings(){
    return bookings.length;
};

function CompletedBookings(){
    let filterbookings = bookings.filter((booking) => {
        let checkinDate = new Date(booking.check_in);
        let today = new Date();
        return (today > checkinDate);
    });
    return filterbookings.length;
}

function UpcomingBookings(){
    
    let filterbookings = bookings.filter((booking) => {
        let checkinDate = new Date(booking.check_in);
        let today = new Date();
        return (today < checkinDate);
    });
    return filterbookings.length;
}

function TotalRevenue(){
    
    let counter = 0;
    let filterbookings = bookings.filter((booking) => {
        counter += parseInt(booking.price_total);
    })
    return counter;
}

export {TotalBookings, CompletedBookings, UpcomingBookings, TotalRevenue};