import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import "./App.css";
import HomePage from "./Home/Home";
import Sidebar from "./Sidebar";
import AllBookingsPage from "./AllBookings/AllBookings";
import BookingDetailsPage from "./BookingDetailsPage/BookingDetailsPage";
import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import Analytics from "./AnalyticsPage/AnalyticsPage";
import AllTasksPage from "./AllTasksPage/AllTasksPage";
import FAQPage from "./FAQPage/FAQPage";
import SettingsPage from "./SettingsPage/SettingsPage";

function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  return (
    <Router>
    <ScrollToTop />
      <Sidebar />
      <div className="content">
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/allbookings" element={<AllBookingsPage />} />
          <Route path="/booking/:id" element={<BookingDetailsPage />} />
          <Route path="/analytics" element={<Analytics />} />
          <Route path="/tasks" element={<AllTasksPage />} />
          <Route path="/faq" element={<FAQPage />} />
          <Route path="/settings" element={<SettingsPage />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
