import React, { useState } from "react";
import "./Accordion.css"; // Εισάγουμε το αρχείο CSS
import { icons } from "../lists";
import { setDate } from "../functions";

export default function ExpandableInfo({ task }) {
  const [isOpen, setIsOpen] = useState(false);
  const [isDone, setIsDone] = useState(task.done);

  let priority_type = "priority-type-div high";
  if (task.priority === "Medium") priority_type = "priority-type-div medium";

  let done_type = "not_done";
  let done_type_text = "In Progress";
  if (task.done) {
    done_type = "done";
    done_type_text = "Completed";
  }

  return (
    <div className="expandable-container fade-in-1">
      <div
        className="expandable-header fade-in-2"
        onClick={() => setIsOpen(!isOpen)}
      >
        <div className="deadline">
          <img className="icon-clock" src={icons.icon_clock} alt="clock" />
          <p>{setDate(task.deadline)} | </p>
          <h4>{task.title}</h4>
        </div>
        <div className="done_type-div">
          <h2 className={done_type}>•</h2>
          <p>{done_type_text}</p>
        </div>
        <div className={priority_type}>
          <p>{task.priority}</p>
        </div>

        <span className={`arrow ${isOpen ? "open" : ""}`}>
          <img src={icons.icon_arrow_down} alt="arrow" />{" "}
        </span>
      </div>
      <div className={`expandable-content ${isOpen ? "show" : ""}`}>
        <p>{task.description}</p>
        <br />
        <div className="flex-horizontal">
          <div className="flex-horizontal-start">
            <h4>Completed</h4>
            <label className="toggle-switch">
              <input
                type="checkbox"
                checked={isDone}
                onChange={() => setIsDone(!isDone)}
              />
              <span className="slider"></span>
            </label>
          </div>
          <p>
            <strong>{task.category}</strong> | Created at:{" "}
            {setDate(task.created_at)}
          </p>
        </div>
      </div>
    </div>
  );
}
