import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Sidebar.css"; // Προσθήκη CSS για styling
import { icons, menu_items } from "./lists";

function MenuItem(item) {
  return (
    <div className="menu-line-item">
      <li>
        <Link to={item.link}>
          <div className="line-left-align-sp-bet">
            <div className="line-left-align">
              <img src={item.icon} alt={item.title} className="petrol" />
              <h4>{item.title}</h4>
            </div>
            <div className="arrow-icon">
              <img className="arrow-sidebar" src={icons.icon_arrow} alt="arrow" />
            </div>
          </div>
        </Link>
      </li>
    </div>
  );
}

function SubmenuTitle(props) {
  return (
    <div className="submenu-title">
      <p>{props.title}</p>
    </div>
  );
}

const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(false);

  const isMobile = window.innerWidth < 600;

  return (
    <>
      {/* Burger menu για μικρές οθόνες */}
      {isMobile && (
        <div className="hamburger-menu">
          <button onClick={() => setIsOpen(!isOpen)}>
            <img src={icons.icon_menud} alt="menu" />
          </button>
        </div>
      )}

      <nav className={`sidebar ${isOpen ? "open" : ""}`}>
        <div className="logo">
          Hospity<strong>.</strong>
        </div>
        <div className="image-container">
          <div className="circle-image">
            <img src={icons.icon_photo_profile} alt="profile-photo" />
          </div>
        </div>

        <div className="text-name">
          <h4>Barbara Spahn</h4>
        </div>

        <div className="text-info">Reservations Manager</div>

        <SubmenuTitle title="Main" />
        <ul>
          {menu_items.slice(0, 2).map((menu_item) => (
            <MenuItem key={menu_item.link} {...menu_item} />
          ))}
        </ul>
        <SubmenuTitle title="Tools" />
        <ul>
          {menu_items.slice(2, 4).map((menu_item) => (
            <MenuItem key={menu_item.link} {...menu_item} />
          ))}
        </ul>
        <SubmenuTitle title="Help" />
        <ul>
          {menu_items.slice(4, 6).map((menu_item) => (
            <MenuItem key={menu_item.link} {...menu_item} />
          ))}
        </ul>
        <button className="exit-button">
          <h4>Log Out</h4>
        </button>
      </nav>
    </>
  );
};

export default Sidebar;
