import bookings from "../bookings";

function OccupPerMonth(wantedMonth) {
  let counter = 0;
  let filteredBookings = bookings.filter((booking) => {
    const month = new Date(booking.check_in).getMonth();
    const monthCheckOut = new Date(booking.check_out).getMonth();
    if (month === wantedMonth) {
      if (monthCheckOut === wantedMonth) {
        counter = counter + parseInt(booking.num_nights);
      } else {
        const daysInMonth = new Date(
          new Date(booking.check_in).getFullYear(),
          wantedMonth + 1,
          0
        ).getDate();
        let daysReserv = daysInMonth - new Date(booking.check_in).getDate() + 1;
        counter = counter + daysReserv;
      }
    } else if (monthCheckOut === wantedMonth) {
      let days = new Date(booking.check_out).getDate();
      counter = counter + days;
    }
    return month === wantedMonth;
  });
  return Math.floor(counter * 100 / 30);
}

export default OccupPerMonth;
