import bookings from "../bookings";

function AverageStayDuration(x){

    let counter = 0;
    let allbookings = bookings.filter((booking) => {
        let temp = parseInt(booking.num_nights);
        if (temp === x){
            counter += 1;
        }
    });
    return counter / bookings.length;
}

export default AverageStayDuration;