import bookings from "./bookings";
import { months } from "./lists";

function setDate(x) {
  const date = x.split("-");
  const day = date[2];
  const month = months[parseInt(date[1], 10) - 1];
  const year = date[0];

  return month + " " + day + ", " + year;
}

function countBookingAcc(account)
{
  let filteredBookings = bookings.filter(
        (booking) => booking.platform === account
      );

      return filteredBookings.length;
}

export {setDate, countBookingAcc};
