import React, { useState } from "react";
import Header from "../Header";
import "../Home/Home.css";
import "../AllBookings/AllBookings.css";
import SearchBox from "../components/SearchBox";
import DropdownMenu from "../components/DropdownMenu";
import tasks from "../tasks";
import ExpandableInfo from "./Accordion";

function AllTasksPage() {
  const [selectedPlatform, setSelectedCategory] = useState("All");
  const [searchQuery, setSearchQuery] = useState("");

  //TODO: Ftiaxe to Dropdown
  let filtTasks =
    selectedPlatform === "All"
      ? tasks
      : tasks.filter((task) => task.category === selectedPlatform);

  filtTasks = filtTasks.filter((task) =>
    Object.values(task).some(
      (value) =>
        typeof value === "string" &&
        value.toLowerCase().includes(searchQuery.toLowerCase())
    )
  );
  return (
    <div className="page-div">
      <Header
        title="All Tasks"
        descr="Easily browse through all your reservations, sorted by check-in
            date. Use the filters to view bookings from specific platforms."
      />
      <div className="top-brd"></div>
      <div className="component-title fade-in-1">Tasks Overview</div>

      <div className="filter-div">
        <DropdownMenu setSelectedPlatform={setSelectedCategory} platforms="tasks"/>
        <SearchBox setSearchQuery={setSearchQuery} />
      </div>

      {filtTasks.map((task, index) => (
        <ExpandableInfo key={task.id} task={task} />
      ))}
    </div>
  );
}

export default AllTasksPage;
