import React, { useState } from "react";
import "../AllTasksPage/Accordion.css";
import { icons } from "../lists";

export default function ExpandableInfo({ faq }) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className="expandable-container-faq fade-in-1">
      <div
        className="expandable-header fade-in-2"
        onClick={() => setIsOpen(!isOpen)}
      >
        <div className="deadline">
          <img className="icon-clock" src={icons.icon_faq} alt="clock" />
          <h4>{faq.title}</h4>
        </div>

        <span className={`arrow ${isOpen ? "open" : ""}`}>
          <img className="white-icon" src={icons.icon_arrow_down} alt="arrow"/>
        </span>
      </div>
      <div className={`expandable-content ${isOpen ? "show" : ""}`}>
        <p className="white-text">{faq.description}</p>
      </div>
    </div>
  );
}
