import React, { useCallback, useState } from "react";
import { PieChart, Pie, Cell } from "recharts";
import { COLORS } from "../lists";
import AverageStayDuration from "../functions/AverageStayDuration"

let data = [];
const titles = ["1 Day", "2 Days", "3 Days", "4 Days", "5 Days"];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = (props) => {
  const { cx, cy, midAngle, innerRadius, outerRadius, percent } = props;
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text
      x={x}
      y={y}
      fill="white"
      textAnchor={x > cx ? "start" : "end"}
      dominantBaseline="central"
      fontSize="24px"
    >
      {`${(percent * 100).toFixed(0)}%`}
    </text>
  );
};
export default function AverageStayDurationChart() {
    data = [];
  for (let i = 1; i < 6; i++) {
    data.push({ Days: i, Percentage: AverageStayDuration(i) });
    if (i === 5) break;
  }
  return (
    <PieChart width={400} height={300}>
      <Pie
        data={data}
        cx="40%"
        cy={140}
        labelLine={false}
        label={renderCustomizedLabel}
        outerRadius="100%"
        dataKey="Percentage"
      >
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
      </Pie>
    </PieChart>
  );
}
