import React from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { COLORS, months } from "../lists";
import OccupPerMonth from "../functions/OccupancyPerMonth"



const data = [
    {
      name: months[0],
      Bookings: OccupPerMonth(0),
    },
    {
      name: months[1],
      Bookings: OccupPerMonth(1),
    },
    {
      name: months[2],
      Bookings: OccupPerMonth(2),
    },
    {
      name: months[3],
      Bookings: OccupPerMonth(3),
    },
    {
      name: months[4],
     Bookings: OccupPerMonth(4),
    },
    {
      name: months[5],
      Bookings: OccupPerMonth(5),
    },
    {
      name: months[6],
      Bookings: OccupPerMonth(6),
    },
    {
      name: months[7],
      Bookings: OccupPerMonth(7),
    },
    {
      name: months[8],
      Bookings: OccupPerMonth(8),
    },
    {
      name: months[9],
      Bookings: OccupPerMonth(9),
    },
    {
      name: months[10],
      Bookings: OccupPerMonth(10),
    },
    {
      name: months[11],
      Bookings: OccupPerMonth(11),
    },
  ];

export default function PlatformReservChart() {
  return (
    <ResponsiveContainer width={"100%"} height={250}>
      <BarChart
        data={data}
        margin={{
          top: 16,
          right: 0,
          left: -25,
          bottom: 0,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        {<XAxis dataKey="name" />}
        <YAxis />
        <Tooltip content={({ active, payload, label }) => {
  if (active && payload && payload.length) {
    return (
      <div className="custom-tooltip">
        <p className="label">{`${label}`}</p>
        <p className="desc">{`${payload[0].value}%`}</p>
      </div>
    );
  }
  return null;
}} />
        
        <Bar dataKey="Bookings" stackId="a" fill={COLORS[3]} radius={[8, 8, 0, 0]}/>
      </BarChart>
    </ResponsiveContainer>
  );
}
