import React, { useState } from "react";
import "./Header.css"; // Το CSS αρχείο μας
import { icons, menu_items } from "./lists";
import accounts from "./accounts";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

export default function Menu() {
  const [isOpen, setIsOpen] = useState(false);
  const navigate = useNavigate();

  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };

  let handleSelect = (path) => {
    setIsOpen(false);
    navigate(path);
  };

  return (
    <div className="menu fade-in-2">
      <button className="menu-btn fade-in-1" onClick={toggleDropdown}>
        <img src={icons.icon_menu} alt="menu" />
      </button>

      {isOpen && (
        <ul className="dropdown-menu-a fade-in-box">
          <a href="/settings">
            <div className="menu-info">
              <div className="text-name">
                <h4>Barbara Spahn</h4>
                <p>Reservations Manager</p>
              </div>
              <div className="image-container">
                <div className="circle-image">
                  <img src={icons.icon_photo_profile} alt="profile-photo" />
                </div>
              </div>
            </div>
          </a>

          <br />
          {menu_items.map((item) => (
            <div key={item.link}>
              <li
                className="dropdown-item-a"
                onClick={() => handleSelect(item.link)}
              >
                <div className="dd-menu-line">
                  <div>
                    <img src={item.icon} alt="icon" className="petrol" />
                    {item.title}
                  </div>

                  <img
                    src={icons.icon_arrow}
                    alt="dd-menu-arrow"
                    className="dd-menu-arrow"
                  />
                </div>
              </li>
            </div>
          ))}
        </ul>
      )}
    </div>
  );
}
