import accounts from "../accounts";
import { setDate } from "../functions";

function TableContLine({ booking, index }) {
  let image = accounts[0].icon;
  for (let i = 1; i < accounts.length; i++) {
    if (booking.platform === accounts[i].code) {
      image = accounts[i].icon;
    }
  }

  let classnamePayment = "payment-status paid fade-in-box";
  let textPayment = "Completed";
  if (booking.payment_status === "pending"){
    classnamePayment = "payment-status pending fade-in-box";
    textPayment = "Not Completed";
  }

  return (
    <div>
      <div className="table-cont-line fade-in-box">
        <div className="table-cont-line-main fade-in-1">
          <img src={image} alt="logo" />
          <h4 className="reservationid fade-in-2">{booking.platform_reserv_code}</h4>
        </div>
        <h4 className="checkin fade-in-box">{setDate(booking.check_in)}</h4>
        <h4 className="nights fade-in-1">{booking.num_nights}</h4>
        <h4 className="guests fade-in-2">{booking.num_people}</h4>
        <div className={classnamePayment}><h4>{textPayment}</h4></div>
      </div>
    </div>
  );
}

export default TableContLine;
