import bookings from "../bookings";

function CountPayment(x) {
  let counter = 0;
  let status = "paid";
  if (x === 0) status = "pending";
  bookings.map((booking) => {
    if (booking.payment_status === status) {
      counter = counter + parseInt(booking.price_total);
    }
  });
  return counter;
}

export default CountPayment;
