import React, { useState } from "react";
import bookings from "../bookings";
import { Link } from "react-router-dom";
import Header from "../Header";
import "../Home/Home.css";
import "./AllBookings.css";
import TableContLine from "../components/TableContLine";
import TableHeader from "../components/TableHeader";
import SearchBox from "../components/SearchBox";
import DropdownMenu from "../components/DropdownMenu";

function AllBookingsPage() {
  const [selectedPlatform, setSelectedPlatform] = useState("All");
  const [searchQuery, setSearchQuery] = useState("");

  let filtBookings =
    selectedPlatform === "All"
      ? bookings
      : bookings.filter((booking) => booking.platform === selectedPlatform);

  filtBookings = filtBookings.filter((booking) =>
    Object.values(booking).some(
      (value) =>
        typeof value === "string" &&
        value.toLowerCase().includes(searchQuery.toLowerCase())
    )
  );
  return (
    <div className="page-div">
      <Header
        title="All Bookings"
        descr="Easily browse through all your reservations, sorted by check-in
            date. Use the filters to view bookings from specific platforms."
      />
      <div className="top-brd"></div>
      <div className="component-title fade-in-1">Details</div>

      <div className="filter-div">
        <DropdownMenu setSelectedPlatform={setSelectedPlatform} />
        <SearchBox setSearchQuery={setSearchQuery} />
      </div>
      <TableHeader />
      {filtBookings.length > 0 ? (
        filtBookings.map((booking, index) => (
          <div key={booking.platform_reserv_code}>
            <div>
              <Link to={`/booking/${booking.platform_reserv_code}`}>
                <TableContLine
                  key={booking.platform_reserv_code}
                  booking={booking}
                  index={index}
                />
              </Link>
            </div>
          </div>
        ))
      ) : (
        <h4 className="no-bookings-message">No bookings found.</h4>
      )}
    </div>
  );
}

export default AllBookingsPage;
